import * as React from "react";

type FullVideoPlayerProps = {
  src?: string;
  poster?: string;
  className?: string;
  videoId?: string;
};

export function FullVideoPlayer({ src, poster, className = "", videoId }: FullVideoPlayerProps) {
  const [videoFailed, setVideoFailed] = React.useState(false);
  const hasSource = Boolean(src);
  const showVideo = hasSource && !videoFailed;

  return (
    <div
      className={`relative w-full aspect-video max-w-5xl mx-auto rounded-2xl overflow-hidden shadow-2xl bg-black border border-[var(--border)] ${className}`}
    >
      {showVideo ? (
        <video
          src={src}
          poster={poster}
          controls
          playsInline
          className="w-full h-full object-cover"
          onError={() => setVideoFailed(true)}
        />
      ) : (
        <div className="w-full h-full relative flex items-center justify-center bg-card text-center p-8">
          {poster ? (
            <img src={poster} alt="Video poster draft" className="absolute inset-0 w-full h-full object-cover" />
          ) : null}
          <div className="relative z-10 rounded-xl border border-border/50 bg-background/85 backdrop-blur-sm px-6 py-4 max-w-2xl">
            <p className="text-sm uppercase tracking-wider text-muted font-mono mb-2">Intro Video Draft</p>
            <p className="text-muted">
              The final Korporus intro video is a separate project scope. This draft keeps the production slot and poster behavior in place.
            </p>
            {videoId ? <p className="text-xs text-muted/80 mt-3 font-mono">video id: {videoId}</p> : null}
          </div>
        </div>
      )}
    </div>
  );
}
